@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700);
body {
	font-family: Roboto, sans-serif;
	margin: 0;
	box-sizing: border-box;
}
#app {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100%;
	min-height: 100vh;
	padding: 30px;
}
#wrapper {
	width: 600px;
	border: 1px solid #333;
	border-radius: 20px;
	box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.75);
}
h1 {
	font-weight: 700;
	font-style: italic;
	text-align: center;
	padding: 0 20px;
	font-size: 2rem;
}
#app-container {
	box-sizing: border-box;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	padding-bottom: 30px;
}
.pad-bank {
	box-sizing: border-box;
	display: flex;
	justify-content: space-around;
	align-content: space-around;
	flex-wrap: wrap;
	width: 280px;
	height: 280px;
	margin: auto;
}
.drum-pad {
	background-color: #342b42;
	color: #fff;
	font-weight: bold;
	font-size: 1.2rem;
	width: 82px;
	height: 82px;
	border-radius: 20px;
	text-align: center;
	line-height: 5rem;
	cursor: pointer;
 border-radius: 6px;
  box-shadow: 3px 4px 0 #c5376d, 0 15px 20px rgba(0, 0, 0, .35);
	
  -webkit-transition: -webkit-box-shadow .1s ease-in-out;
  -webkit-transition: box-shadow .1s ease-in-out;
  transition: box-shadow .1s ease-in-out;
	
		background-color: #ec528d;
    background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from(hsla(338, 90%, 80%, .8)), to(hsla(338, 90%, 70%, .2)));
    background-image: -webkit-linear-gradient(hsla(338, 90%, 80%, .8), hsla(338, 90%, 70%, .2));
    background-image: -o-linear-gradient(hsla(338, 90%, 80%, .8), hsla(338, 90%, 70%, .2));
}
.drum-pad:active{
    box-shadow: 0 0px 0 #c5376d, 0 12px 10px rgba(0, 0, 0, .3);
}

.drum-pad:active {
    -webkit-transform: translate(1px, 2px);
    transform: translate(1px, 2px);
}

#controls {
	box-sizing: border-box;
	padding: 20px 40px;
	display: flex;
	flex-wrap: wrap;
	align-content: space-evenly;
	width: 280px;
}
#display {
	width: 100%;
	height: 25px;
	text-align: center;
	font-size: 1.5rem;
	font-weight: 700;
	padding: 10px;
	background-color: #d3d3d3;
	border: 1px solid #c5376d;
	border-radius: 6px;
}
#volume {
	margin: auto;
	margin-top: 12%;
}
.fas {
	color: #c5376d;
}

/* SLIDER */
label {
	font-weight: 700;
	text-align: right;
}
.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;   
  background: #d3d3d3;
  outline: none;
  -webkit-transition: .2s;
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 25px;
  border-radius: 50%; 
  background: #c5376d;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #4CAF50;
  cursor: pointer;
}

@media (min-width: 576px) {
	h1 {
		text-align: right;
	}
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

